// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent, act, waitFor } from '@testing-library/react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector';

// üî• Mocks optimis√©s pour la version 1 (la bonne version)
const mockStart = jest.fn((callback) => {
  // Ex√©cution diff√©r√©e pour simuler les animations
  if (callback) {
    setTimeout(() => {
      act(() => {
        callback();
      });
    }, 0);
  }
});

const mockTiming = jest.fn(() => ({ start: mockStart }));
const mockParallel = jest.fn(() => ({ start: mockStart }));
const mockMultiply = jest.fn((scale1, scale2) => ({
  interpolate: jest.fn((config) => config.outputRange[1]) // Simule la valeur finale
}));

const MockAnimatedValue = jest.fn().mockImplementation((initialValue = 0) => {
  const mockValue = {
    interpolate: jest.fn((config) => {
      // Simule l'interpolation bas√©e sur la valeur initiale
      const ratio = initialValue / (config.inputRange[config.inputRange.length - 1] || 1);
      const outputIndex = Math.min(Math.floor(ratio * config.outputRange.length), config.outputRange.length - 1);
      return config.outputRange[outputIndex] || config.outputRange[0];
    }),
    _value: initialValue,
    setValue: jest.fn((value) => {
      mockValue._value = value;
    })
  };
  return mockValue;
});

jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: MockAnimatedValue,
      timing: mockTiming,
      parallel: mockParallel,
      multiply: mockMultiply,
      View: ({ children, style, onLayout, ...props }) => {
        const { View: RNView } = RN;
        return <RNView style={style} onLayout={onLayout} {...props}>{children}</RNView>;
      },
    },
    ScrollView: ({ children, ...props }) => {
      const { ScrollView: RNScrollView } = RN;
      return <RNScrollView {...props}>{children}</RNScrollView>;
    },
    TouchableOpacity: RN.TouchableOpacity,
    Text: RN.Text,
    View: RN.View,
  };
});

jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return <View testID="linear-gradient" {...props}>{children}</View>;
  },
}));

jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () => StyleSheet.create({
    container: { flex: 1 },
    backgroundGradient: { flex: 1 },
    scrollView: { flexGrow: 0 },
    scrollContent: { paddingHorizontal: 16 },
    categoryItemWrapper: { marginHorizontal: 4 },
    categoryTouchable: { borderRadius: 20 },
    selectedCategoryItem: { 
      paddingHorizontal: 16, 
      paddingVertical: 8,
      borderRadius: 20,
    },
    selectedInner: { 
      flexDirection: 'row', 
      alignItems: 'center' 
    },
    selectedCategoryText: { 
      color: '#FFFFFF',
      fontWeight: 'bold'
    },
    sparkleContainer: { marginLeft: 4 },
    sparkle: { fontSize: 12 },
    categoryItem: {
      paddingHorizontal: 16,
      paddingVertical: 8,
      borderRadius: 20,
      borderWidth: 1,
    },
    categoryInner: {
      alignItems: 'center',
      justifyContent: 'center',
    },
    categoryText: { 
      fontWeight: '500'
    },
  });
});

describe('CategorySelector', () => {
  const categories = [
    { id: '1', name: 'Grammar' },
    { id: '2', name: 'Vocabulary' },
    { id: '3', name: 'Reading' },
  ];

  const defaultProps = {
    categories,
    onSelectCategory: jest.fn(),
    primaryColor: '#5E60CE',
  };

  beforeEach(() => {
    jest.clearAllMocks();
    MockAnimatedValue.mockClear();
    mockTiming.mockClear();
    mockParallel.mockClear();
    mockStart.mockClear();
    mockMultiply.mockClear();
  });

  afterEach(() => {
    jest.runOnlyPendingTimers();
    jest.useRealTimers();
  });

  // ‚úÖ Tests de base
  describe('Basic rendering', () => {
    it('should render all categories correctly', () => {
      const { getByText } = render(<CategorySelector {...defaultProps} />);
      
      expect(getByText('Grammar')).toBeTruthy();
      expect(getByText('Vocabulary')).toBeTruthy();
      expect(getByText('Reading')).toBeTruthy();
    });

    it('should display sparkle for selected category only', () => {
      const { getByText, queryByText, rerender } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );
      
      expect(getByText('‚ú®')).toBeTruthy();
      
      // V√©rifier qu'il n'y a qu'un seul sparkle
      rerender(<CategorySelector {...defaultProps} selectedCategory="2" />);
      expect(getByText('‚ú®')).toBeTruthy();
    });

    it('should handle empty categories gracefully', () => {
      const { container } = render(
        <CategorySelector 
          categories={[]} 
          onSelectCategory={jest.fn()} 
        />
      );
      expect(container).toBeTruthy();
    });
  });

  // üî• Tests d'interaction utilisateur
  describe('User interactions', () => {
    it('should call onSelectCategory when unselected category is pressed', async () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          {...defaultProps}
          onSelectCategory={onSelectCategory}
          selectedCategory="1"
        />
      );

      fireEvent.press(getByText('Vocabulary'));
      
      await waitFor(() => {
        expect(onSelectCategory).toHaveBeenCalledWith('2');
      });
    });

    it('should not call onSelectCategory for already selected category', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector
          {...defaultProps}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      fireEvent.press(getByText('Grammar'));
      expect(onSelectCategory).not.toHaveBeenCalled();
    });

    it('should handle press in/out events', () => {
      const { getByText } = render(
        <CategorySelector {...defaultProps} />
      );

      const grammarButton = getByText('Grammar');
      
      expect(() => {
        fireEvent(grammarButton, 'pressIn');
        fireEvent(grammarButton, 'pressOut');
      }).not.toThrow();
      
      expect(mockTiming).toHaveBeenCalled();
    });
  });

  // üéØ Tests d'animations
  describe('Animations', () => {
    beforeEach(() => {
      jest.useFakeTimers();
    });

    it('should initialize animations for all categories', () => {
      render(<CategorySelector {...defaultProps} />);
      
      // V√©rifier que les animations sont cr√©√©es pour chaque cat√©gorie
      expect(MockAnimatedValue).toHaveBeenCalledTimes(6); // 3 cat√©gories x 2 animations (selection + press)
    });

    it('should trigger animations when selectedCategory changes', async () => {
      const { rerender } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      mockTiming.mockClear();
      mockParallel.mockClear();

      await act(async () => {
        rerender(
          <CategorySelector {...defaultProps} selectedCategory="2" />
        );
      });

      await waitFor(() => {
        expect(mockParallel).toHaveBeenCalled();
        expect(mockTiming).toHaveBeenCalledTimes(2); // D√©s√©lection + S√©lection
      });
    });

    it('should prevent multiple simultaneous animations', async () => {
      const { rerender } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      mockParallel.mockClear();

      // Premier changement
      rerender(<CategorySelector {...defaultProps} selectedCategory="2" />);
      
      // Deuxi√®me changement imm√©diat (devrait √™tre ignor√©)
      rerender(<CategorySelector {...defaultProps} selectedCategory="3" />);

      await waitFor(() => {
        // Seule la premi√®re animation devrait √™tre d√©clench√©e
        expect(mockParallel).toHaveBeenCalledTimes(1);
      });
    });
  });

  // üîÑ Tests de gestion dynamique des cat√©gories
  describe('Dynamic category management', () => {
    it('should handle category addition', async () => {
      const { rerender, getByText } = render(
        <CategorySelector {...defaultProps} />
      );

      const newCategories = [
        ...categories,
        { id: '4', name: 'Writing' }
      ];

      await act(async () => {
        rerender(
          <CategorySelector
            {...defaultProps}
            categories={newCategories}
          />
        );
      });

      expect(getByText('Writing')).toBeTruthy();
    });

    it('should handle category removal', async () => {
      const { rerender, queryByText } = render(
        <CategorySelector {...defaultProps} />
      );

      const reducedCategories = [categories[0]];

      await act(async () => {
        rerender(
          <CategorySelector
            {...defaultProps}
            categories={reducedCategories}
          />
        );
      });

      expect(queryByText('Vocabulary')).toBeFalsy();
      expect(queryByText('Reading')).toBeFalsy();
    });

    it('should maintain animations when categories change', () => {
      const { rerender } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      MockAnimatedValue.mockClear();

      rerender(
        <CategorySelector 
          {...defaultProps} 
          categories={[...categories, { id: '4', name: 'Writing' }]}
          selectedCategory="1"
        />
      );

      // Nouvelles animations cr√©√©es pour la nouvelle cat√©gorie
      expect(MockAnimatedValue).toHaveBeenCalled();
    });
  });

  // üì± Tests d'accessibilit√©
  describe('Accessibility', () => {
    it('should have proper accessibility attributes', () => {
      const { getByText } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      const grammarButton = getByText('Grammar').parent;
      expect(grammarButton.props.accessibilityRole).toBe('button');
      expect(grammarButton.props.accessibilityLabel).toBe('Grammar');
      expect(grammarButton.props.accessibilityState).toEqual({ selected: true });
    });
  });

  // üé® Tests de style et couleurs
  describe('Styling and colors', () => {
    it('should apply custom primary color', () => {
      const customColor = '#FF6B6B';
      render(
        <CategorySelector 
          {...defaultProps} 
          primaryColor={customColor}
        />
      );

      // Le composant devrait accepter la couleur sans erreur
      expect(() => {
        render(
          <CategorySelector 
            {...defaultProps} 
            primaryColor={customColor}
          />
        );
      }).not.toThrow();
    });
  });

  // üîß Tests de la fonction areEqual
  describe('areEqual memoization', () => {
    it('should return true for identical props', () => {
      const props = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }],
        onSelectCategory: jest.fn()
      };
      expect(areEqual(props, props)).toBe(true);
    });

    it('should return false for different selectedCategory', () => {
      const props1 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }]
      };
      const props2 = {
        selectedCategory: '2',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }]
      };
      expect(areEqual(props1, props2)).toBe(false);
    });

    it('should return false for different primaryColor', () => {
      const props1 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }]
      };
      const props2 = {
        selectedCategory: '1',
        primaryColor: '#FF0000',
        categories: [{ id: '1', name: 'Cat1' }]
      };
      expect(areEqual(props1, props2)).toBe(false);
    });

    it('should return true for same category reference', () => {
      const categories = [{ id: '1', name: 'Cat1' }];
      const props1 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories
      };
      const props2 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories
      };
      expect(areEqual(props1, props2)).toBe(true);
    });

    it('should return true for equivalent categories with different references', () => {
      const props1 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }, { id: '2', name: 'Cat2' }]
      };
      const props2 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }, { id: '2', name: 'Cat2' }]
      };
      expect(areEqual(props1, props2)).toBe(true);
    });

    it('should return false for different category content', () => {
      const props1 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat1' }]
      };
      const props2 = {
        selectedCategory: '1',
        primaryColor: '#5E60CE',
        categories: [{ id: '1', name: 'Cat2' }]
      };
      expect(areEqual(props1, props2)).toBe(false);
    });
  });

  // ‚ö° Tests de performance
  describe('Performance', () => {
    it('should not recreate animations unnecessarily', () => {
      const { rerender } = render(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      const initialCallCount = MockAnimatedValue.mock.calls.length;

      // Re-render avec les m√™mes props
      rerender(
        <CategorySelector {...defaultProps} selectedCategory="1" />
      );

      // Pas de nouvelles animations cr√©√©es
      expect(MockAnimatedValue.mock.calls.length).toBe(initialCallCount);
    });
  });
});